.phone-enter {
  background: url("../img/phone-enter-imgs/bg.png") top 65% right 0 no-repeat;

  h2 {
    margin-bottom: 70px;
    @media (max-width: 1200px) {
      margin-top: 10%;
      margin-bottom: 15%;
    }
    @media (max-width: 576px) {
      margin-top: 50px;
      margin-bottom: 10%;
    }
  }

  margin-bottom: 200px;


}

.phone-enter__wrapper {
  display: flex;
}

.phone-enter__img-container {
  width: 34%;
  @media (max-width: 1200px) {
    width: 25%;
    margin-right: 5%;
  }
  @media (max-width: 992px) {
    display: none;
  }

}

.phone-enter__content {
  width: 65%;
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 768px) {
    width: 90%;
  }
  @media (max-width: 576px) {
    width: 100%;
  }
}

.phone-enter__interfaces {
  width: 100%;
  margin-bottom: 360px;
  display: flex;
  @media (max-width: 1200px) {
    margin-bottom: 10%;
  }
  @media (max-width: 576px) {
    flex-wrap: wrap;
  }
}

.phone-enter__interfaces-item {
  width: 43%;
  margin-right: 94px;
  @media (max-width: 576px) {
    text-align: center;
    width: 100%;
    margin-right: 0;
    margin-bottom: 40px;
  }

  &:last-child {
    margin-right: 0;
  }

  h4 {
    margin-bottom: 5px;
  }
}

.phone-enter__interfaces-img {
  width: 50px;
  height: 50px;
  margin-bottom: 14px;
}

.phone-enter__numbers {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-left: -60px;
  @media (max-width: 768px) {
    flex-wrap: wrap;
  }
  @media (max-width: 576px)  {
    margin-left: 0;
    text-align: center;

  }
}

.phone-enter__numbers-item {
  margin-right: 60px;
  margin-left: 60px;
  width: auto;
  position: relative;
  @media (max-width: 768px) {
    margin-bottom: 30px;
  }
  @media (max-width: 576px) {

    margin: 0 auto;
    margin-bottom: 30px;
  }

  &:not(:first-child):before {
    position: absolute;
    content: '';
    height: 50px;
    width: 2px;
    background-color: rgb(10, 215, 141, .2);
    left: -60px;
    top: 33%;
    @media (max-width: 768px) {
      display: none;
    }

  }


}

.phone-enter__heading-number {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  font-size: 55px;
  line-height: 65px;
  color: #FFFFFF;
  margin-bottom: 5px;
}
